import {Bread} from 'Bread'
import React, {PureComponent} from 'react'
import {Input, Button, message} from 'antd'
import {setVariant} from 'Variant'
import {getData} from 'util'
import {API_PACKAGE_APPLY_SHIPPEDORDERCODE, API_PACKAGE_PRINT_SHIPPEDORDERCODE} from 'api'
const ORDER_LIST = 'wms/packageList'

export default class App extends PureComponent {
  constructor() {
    super()
    this.state = {
      old: '',
      neww: '',
      submitData: {}
    }
    this.handleOldClick = this.handleOldClick.bind(this)
    this.handleNewClick = this.handleNewClick.bind(this)
  }
  componentDidMount() {
    this.textInput.focus()
  }

  handleOldClick() {
    if (!this.state.old) {
      message.error('请输入包裹单或运单号')
    }
    getData({api: API_PACKAGE_PRINT_SHIPPEDORDERCODE, urlParams: [this.state.old]}).then(data => {
      this.newOrOld(data)
    })
  }

  handleNewClick() {
    if (!this.state.neww) {
      message.error('请输入出库单号')
    }
    getData({api: API_PACKAGE_APPLY_SHIPPEDORDERCODE, urlParams: [this.state.neww]}).then(data => {
      const {packageCode} = data.result
      if (packageCode) {
        message.success(`新包裹单${packageCode}申请成功`)
      }
      this.newOrOld(data)
    })
  }

  // 判断是新的还是旧的
  newOrOld(data) {
    const {businessTypeCode, packageId, packageCode} = data.result
    // 判断是新的还是旧的
    if (businessTypeCode == 22) {
      this.setState({
        submitData: data.result
      }, () => {
        this.form.submit()
      })
    } else {
      setVariant('wms/packageList', [packageId])
      setVariant('wms/packageListNum', [packageCode])
      window.open('/wms/packageList/Print/1', 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
    }
  }
  render() {
    const {old, neww, submitData} = this.state
    return (
      <section >
        <Bread route={['出库管理', '包裹管理', '包裹单打印']} />
        <h2>包裹单打印</h2>
        <div>
          <label style={{display: 'inline-block', width: '130px'}}>已有包裹单再打印：</label>
          <Input
            placeholder={'请输入包裹单或运单号'}
            value={old}
            ref={el => this.textInput = el}
            style={{width: 180, marginRight: '16px'}}
            onChange={(e) => {
              this.setState({old: e.target.value})
            }}
            onPressEnter={this.handleOldClick} />
          <Button type='primary' onClick={this.handleOldClick} disabled={old?false:true}>打印</Button>
        </div>
        <div style={{marginTop: '16px'}}>
          <label style={{display: 'inline-block', width: '130px'}}>申请新包裹单：</label>
          <Input
            value={neww}
            placeholder={'请输入出库单号'}
            style={{width: 180, marginRight: '16px'}}
            onChange={(e) => {
              this.setState({neww: e.target.value})
            }}
            onPressEnter={this.handleNewClick} />
          <Button type='primary' onClick={this.handleNewClick} disabled={neww?false:true}>打印</Button>
        </div>
        <form style={{display: 'none'}} ref={el => this.form = el} action="http://www.kdniao.com/External/PrintOrder.aspx" method="post" target="_blank">
          <div>
            <div><input type="text" id="RequestData" name="RequestData" value={encodeURI(submitData.requestData)} onChange={() => {}}/></div>
            <div><input type="text" id="EBusinessID" name="EBusinessID" value={submitData.ebusinessID} onChange={() => {}}/></div>
            <div><input type="text" id="DataSign" name="DataSign" value={submitData.dataSign} onChange={() => {}}/></div>
            <div><input type="text" id="IsPreview" name="IsPreview" value={submitData.isPreview} onChange={() => {}}/></div>
          </div>
        </form>
      </section>
    )
  }
}



